// @flow

import * as React from 'react';
import { Image as ImageBase, View } from 'react-native-web';
import { useDOMSize } from './Layout.js';
import { useTheme } from './Style.js';

type Props = {|
  source: string,
  previewSource?: ?string,
  size: {| width: number, height: number |},
|};

export function Image({
  source,
  previewSource,
  size: { width, height },
}: Props) {
  let [size, ref] = useDOMSize();
  let [loaded, setLoaded] = React.useState(false);
  let theme = useTheme();
  let ratio = height / width;
  return (
    <View ref={ref} style={{ width: '100%' }}>
      {size == null ? null : (
        <ImageBase
          source={source}
          onLoad={() => setLoaded(true)}
          style={{
            width: size.width,
            height: size.width * ratio,
            borderRadius: 2,
            backgroundColor: loaded
              ? undefined
              : theme.backgroundSecondaryColor,
          }}
          resizeMode="contain"
        />
      )}
    </View>
  );
}
